
$dark: #333;
$light: #fff;
$contrast: #FF0;

$fontSizeTitle:42px;
$fontSizeText:16px;

$buttonTop:125px;

:root {
    //font-size: 25vmin;
    font-family: 'Roboto', sans-serif;
    width: 100%;
    height: 100%;
}

body {
    margin: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    background-color: $light;
    color: $dark;
    transition: all 0.15s ease-out;

    &.dark-theme {
        background-color: $dark;
        color: $light;
    }
    &.contrast-theme {
      background-color: black;
      color: $contrast;
    }

    &.font-s{
      .title{
        font-size: calc($fontSizeTitle);
      }
      .text{
        font-size: calc($fontSizeText);
      }
      .font{
        font-size: calc($fontSizeText);
      }
      .buttonClicks{
        top: calc(50% - 48px + $buttonTop);
      }
    }

    &.font-m{
      .title{
        font-size: calc($fontSizeTitle*1.25);
      }
      .text{
        font-size: calc($fontSizeText*1.25);
      }
      .font{
        font-size: calc($fontSizeText*1.25);
      }
      .buttonClicks{
        top: calc(50% - 48px + $buttonTop*1.5);
      }
    }

    &.font-b{
      .title{
        font-size: calc($fontSizeTitle*1.5);
      }
      .text{
        font-size: calc($fontSizeText*1.5);
      }
      .font{
        font-size: calc($fontSizeText*1.5);
      }
      .buttonClicks{
        top: calc(50% - 48px + $buttonTop*2);
      }
    }
}


//overlay+alert
.overlay{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;

  background: #121212;
  opacity: 0.5;

  z-index: 10;
  
  &.hidden{
    visibility: hidden;
  }
}
.alert{
  background: #FFFFFF;

  z-index: 11;
  &.hidden{
    visibility: hidden;
  }
  
  position: absolute;
  @media (min-width:920px) {
    width: 920px;
    height: 210px;
    left: calc(50% - 920px/2);
    top: calc(50% - 210px/2);
  }
  @media (max-width:920px) {
    width: 100%;
    height: auto;
    min-height: 210px;
    left: 0;
    top: 0;
  }
  

}
.alContent{
  @media (min-width:920px) {
    position: absolute;
    width: 380px;
    height: 98px;
    left: calc(50% - 380px/2 - 355px/2);
    top: calc(50% - 98px/2);
  }
  @media (max-width:920px) {
    position: absolute;
    width: 70%;
    height: auto;
    min-height: 98px;
    left: 15%;
    top: calc(50% - 98px/2);
  }
  
}

.xButton{
  position: absolute;
  width: 24px;
  height: 24px;
  left: calc(50% - 24px/2 + 800px/2);
  top: calc(50% - 24px/2 - 210px/4);

  background: #121212;
}

.reset{
  position: absolute;
  width: 64px;
  height: 64px;
  left: calc(50% - 64px/2 + 800px/2);
  top: calc(50% - 64px/2 + 210px/4);
  &.hidden{
    visibility: hidden;
  }
}
//


//main module
.module{
  position: relative;
  width: 1440px;
  height: 960px;
}
//


//WCAG menu
.buttons {
  position: absolute;
  width:300px;
  height: 48px;
  left: calc(50% - 300px/2);
  top: calc(80px/2);
  & .media{
    width: 100%;
  } 
  & button {
    background: none;
    border: none;
    cursor: pointer;
    height: 48px;
    outline: none;
    padding: 0;
    width: 48px;
    float: left;
  }
}
//

//themes and font size buttons
.change-theme-btn {
  font: inherit;
  padding: 0.1em 0.25em;
  border: 0;
  outline: 0;
  color: $light;
  cursor: pointer;
  transition: all 0.15s ease-out;
  position: absolute;
  top: 0;
  background-color: transparent;
  overflow: hidden;
  width: 48px;
  height: 48px;

  &.dark{
    left: calc(100% - 150px);
  }

  &.contrast{
    left: calc(100% - 102px);
  }

  &.font{
    left: calc(100% - 48px);
  }

  &::before, &::after {
      content: '';
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $dark;
      transition: all 0.15s ease-out;
  }

  &::after {
      background-color: $light;
      left: 100%;
  }

  &:hover {
      text-decoration: underline;
  }

  .dark-theme & {
      color: $dark;

      &::before {
          left: -100%;
      }

      &::after {
          left: 0;
      }
  }
  .contrast-theme & {
    color: $contrast;

    &::before {
        left: -100%;
    }

    &::after {
      background-color: black;
        left: 0;
    }
  }
}
//

//text2speech buttons
#play {
	background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/play.svg);
  &.played {
    background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/play1.svg);
  }}

#pause {
	background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/pause.svg);
&.paused {
	background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/pause1.svg);
}}

#stop {
	background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/stop.svg);
&.stopped {
	background-image: url(https://rpsthecoder.github.io/js-speech-synthesis/stop1.svg);
}}
//


//image
  .img{
    position: absolute;
    left: 230px;
    top: 96px;
  }
//

//text content
  .right{
    position: absolute;
    width: 380px;
    height: 250px;
    left: 830px;
    top: 136px;
  }

  //title
  .title{
    position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 380px/2);
  top: calc(50% - 50px/2 - 96px);
  
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: $fontSizeTitle;
  line-height: 120%;
  
    &.alTitle{
      position: absolute;
      width: 380px;
      height: 50px;
      left: calc(50% - 380px/2);
      top: 0;
      
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 700;
      line-height: 120%;
    }
  }
  
  //text
  .text{
  position: absolute;
  width: 380px;
  height: 96px;
  left: calc(50% - 380px/2);
  top: calc(50% - 96px/2);
  
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  font-size: $fontSizeText;
  line-height: 150%;
    &.buttonTxt{
      width: 48px;
      height: 16px;
      left: calc(50% - 48px/2);
      top: calc(50% - 16px/2);
  
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 700;
      font-size: $fontSizeText;
      line-height: 100%;
  
      color: #FFFFFF;
  
      flex: none;
      order: 0;
      flex-grow: 0;
    }
  
    &.alText{
      position: absolute;
      width: 380px;
      height: 24px;
      left: calc(50% - 380px/2);
      top: calc(50% - 24px/2 + 37px);
  
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 300;
      line-height: 150%;
    }
  }
  
  //click counter button
  .buttonClicks{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 16px 40px;
    gap: 10px;
    
    position: absolute;
    width: 128px;
    height: 48px;
    left: 0;
    top: calc(50% - 48px + $buttonTop);
    
    background: #E4C1AE;
    border-radius: 32px;
    border: 0;
  }
//
